export class FinalScreen {
    constructor(root) {
        Object.defineProperty(this, "root", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: void 0
        });
        Object.defineProperty(this, "container", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: null
        });
        Object.defineProperty(this, "active", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: false
        });
        this.root = root;
    }
    mount() {
        this.container = document.createElement("div");
        this.container.className = "fd-final";
        this.container.innerHTML = `
      <div class="fd-final__content">
        <div class="fd-final__atmosphere"></div>
        <p class="fd-final__description"></p>
        <div class="fd-final__stats"></div>
      </div>
    `;
        this.container.hidden = true;
        this.root.appendChild(this.container);
    }
    show(payload) {
        if (!this.container) {
            return;
        }
        const atmosphere = this.container.querySelector(".fd-final__atmosphere");
        const description = this.container.querySelector(".fd-final__description");
        const stats = this.container.querySelector(".fd-final__stats");
        if (atmosphere) {
            atmosphere.textContent = payload.atmosphere;
        }
        if (description) {
            description.textContent = payload.description;
        }
        if (stats) {
            stats.innerHTML = "";
            stats.append(this.createStat("Total casualties", payload.stats.casualties), this.createStat("Final morale", `${payload.stats.morale}%`), this.createStat("Final threat", `${payload.stats.threat}%`), this.createStat("NPCs alive", payload.stats.survivors), this.createStat("Structures intact", payload.stats.structures));
        }
        this.container.hidden = false;
        this.container.classList.add("fd-final--visible");
        this.active = true;
    }
    hide() {
        if (this.container) {
            this.container.hidden = true;
            this.container.classList.remove("fd-final--visible");
        }
        this.active = false;
    }
    isVisible() {
        return this.active;
    }
    createStat(label, value) {
        const row = document.createElement("div");
        row.className = "fd-final__stat";
        const name = document.createElement("span");
        name.textContent = label;
        const val = document.createElement("span");
        val.textContent = String(value);
        row.append(name, val);
        return row;
    }
}
export function summarizeFinalStats(payload) {
    return {
        casualties: payload.casualties,
        morale: payload.morale,
        threat: payload.threat,
        survivors: payload.survivors,
        structures: payload.structures
    };
}
